import React, { Component, Fragment } from "react";
import Modal, { Themes, ModalSize } from "../../../components/Modal/Modal";
import { ApplicationStepsTypes } from "../ApplicationSteps/ApplicationSteps";
import ChooseExamCenter, {
  LocationTable,
  LocationTableInterface,
} from "./ChooseExamCenter";
import * as Interfaces from "../../../interfaces/index";
import { Redirect } from "react-router-dom";
import { connect } from "react-redux";
import {
  Auth,
  initiateApplication,
  selectPosition,
  createApplication,
  applicationStoreInterface,
  addLocation,
  initialApplicationInterface,
  removeLocation,
  System,
  PushPopupAlert,
  applicationPeriodsStoreInterface,
  LoadCandidateApplications,
  CandidateApplicationsStoreInterface,
  AddUserApplication,
} from "../../../actions";
import { StoreState } from "../../../reducers";
import { course } from "../../../interfaces/index";
import {
  ApplicationInterface,
  GetApplicationResponse,
  GetApplicationsAPI,
  NoRoomsInterface,
} from "../../../interfaces/application";
import { MdLocationOn } from "react-icons/md";
import axios from "axios";
import { API } from "../../../utils/api";
import Loading from "../../../components/Loading/Loading";
import Alert from "../../../components/Alert/Alert";
import { AlertInterface } from "../../../components/Alert/Alert";
import { LocationAPI } from "../../../interfaces/location";
import { PopupAlertData } from "../../../components/Alert/Popup";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";

interface ExamCenterState {
  centersModal: boolean;
  selectedCourse: course | null;
  redirect: boolean;
  applicationError: string;
  applicationData: SubmitApplicationAPI | null;
  loading: boolean;
  crated_application: string | null;
  noRoomsApplication: NoRoomsInterface[] | null | [];
  locations: LocationAPI[] | null;
}

export interface sentExamInterface {
  course_id: string;
  proctoring_type: string;
  center_id: string;
  district_id: string;
}

interface ExamCenterProps {
  currentPosition: Interfaces.PositionAPI | null;
  setCurrentPosition: (position: Interfaces.PositionAPI | null) => void;
  setApplicationStep: (step: ApplicationStepsTypes) => void;
  application: applicationStoreInterface;
  addLocation: (
    course_id: string,
    course_name: string,
    course_code: string,
    location: LocationTableInterface,
    exam_id: string
  ) => void;
  auth: Auth;
  createApplication: (
    user_id: string,
    application: ApplicationInterface
  ) => void;
  initiateApplication: (initial: initialApplicationInterface) => void;
  removeLocation: (course_id: string) => void;
  system: System;
  PushPopupAlert: (alert: PopupAlertData) => void;
  periods: applicationPeriodsStoreInterface;
  LoadCandidateApplications: (
    user_id: string,
    callback: (loading: boolean) => void
  ) => void;
  candidateApplications: CandidateApplicationsStoreInterface;
  AddUserApplication: (application: GetApplicationsAPI) => void;
}

export interface SubmitApplicationAPI {
  user_id: string;
  position: string;
  application_period: string;
  exams: {
    course_id: string;
    proctoring_type: string;
    center_id: string;
  }[];
}

export class _ExamCenter extends Component<ExamCenterProps, ExamCenterState> {
  constructor(props: ExamCenterProps) {
    super(props);

    this.state = {
      centersModal: false,
      selectedCourse: null,
      redirect: false,
      applicationError: "",
      applicationData: null,
      loading: false,
      noRoomsApplication: null,
      crated_application: null,
      locations: null,
    };
  }
  closeModal = () => {
    this.setState({ centersModal: false });
  };
  courseStatus = (course: course) => {
    if (
      this.props.application.apply?.selectedLocation.find(
        (itm) => itm.courseId === course.course_id
      )
    ) {
      return this.props.application.apply?.selectedLocation.find(
        (itm) => itm.courseId === course.course_id
      );
    }
    // return false;
  };

  LoadLocations = async () => {
    this.setState({ loading: true });
    try {
      const res = await axios.get<LocationAPI[]>(
        `${API.APPLICATION}/roomlocation`
      );
      if (res.status === 200) {
        if (
          this.state.locations === null ||
          this.state.locations.length === 0
        ) {
          this.setState({ locations: res.data });
          if (this.state.locations !== null) {
            if (this.state.locations.length > 0) {
              this.loadMyApplications();
            } else {
              this.setState({ loading: false });
            }
          } else {
            this.setState({ loading: false });
          }
        } else {
          this.setState({ loading: false });
        }
      }
    } catch (error: any) {
      this.setState({ loading: false });
      console.log("Locations: ", { ...error });
    }
  };

  loadMyApplications = async () => {
    if (this.props.auth.user.user_id) {
      this.setState({ loading: true });
      let response: GetApplicationsAPI[] | null = null;
      if (
        this.props.candidateApplications.candidate_applications === null &&
        this.props.auth.user
      ) {
        try {
          const res = await axios.get<GetApplicationsAPI[]>(
            `${API.APPLICATION}/application/${this.props.auth.user.user_id}`
          );
          if (res.status === 200) {
            response = res.data;
            for (let application of response) {
              this.props.AddUserApplication(application);
            }
          } else {
            response = [];
          }
        } catch (error: any) {
          response = [];
          console.log("Appl Err: ", { ...error });
        }
      } else {
        response = this.props.candidateApplications.candidate_applications;
      }
      if (
        response !== null &&
        this.props.application.apply !== null &&
        this.props.application.apply?.selectedPosition !== undefined &&
        this.props.application.apply?.selectedPosition !== null &&
        this.state.locations !== null
      ) {
        for (let i in response) {
          if (
            response[i].position.position_id ===
            this.props.application.apply?.selectedPosition?.position_id
          ) {
            if (
              this.props.periods.applicationPeriods !== null &&
              this.props.periods.applicationPeriods.find(
                (itm) => itm.is_active === true
              )?.application_session_id ===
                response[0].application_session.application_session_id
            ) {
              this.props.PushPopupAlert({
                title: "Already created this application!",
                description:
                  "The application already exists for the current period, You can not create same application twice!",
                theme: PopupAlertTheme.warning,
              });
              return this.setState({ redirect: true });
            }
          }
          let currentCourses: {
            course_name: string;
            course_code: string;
            description: string;
            school_levels: string[];
            course_id: string;
          }[] = this.props.application.apply.selectedPosition.courses;
          let exams = response[i].exams;
          if (currentCourses !== undefined) {
            for (let x in currentCourses) {
              if (currentCourses[x] !== undefined) {
                if (
                  exams.find(
                    (itm) => itm.course_id === currentCourses[x].course_id
                  )
                ) {
                  let selectedCenter = exams.find(
                    (itm) => itm.course_id === currentCourses[x].course_id
                  )?.center_id;
                  if (LocationTable(this.state.locations).length > 0) {
                    if (
                      LocationTable(this.state.locations).find(
                        (itm) => itm.center_id === selectedCenter
                      ) !== undefined
                    ) {
                      let selectedCenterId = LocationTable(
                        this.state.locations
                      ).find((itm) => itm.center_id === selectedCenter);
                      if (selectedCenterId !== undefined) {
                        this.props.addLocation(
                          currentCourses[x].course_id,
                          currentCourses[x].course_name,
                          currentCourses[x].course_code,
                          selectedCenterId,
                          currentCourses[x].course_id
                        );
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
      this.setState({ loading: false });
    }
  };

  locationStatus = (courses: course[]) => {
    let count: number = 0;
    for (let i in courses) {
      if (
        this.props.application.apply?.selectedLocation.find(
          (itm) => itm.courseId === courses[i].course_id
        )
      ) {
        count = count + 1;
      }
    }
    if (count === courses.length) {
      return true;
    } else {
      return false;
    }
  };

  updateLocation = async (
    application_id: string,
    exams: sentExamInterface[]
  ) => {
    this.setState({ loading: true });
    for (let i in exams) {
      try {
        const res = await axios.post(
          `${API.APPLICATION}/application/updatemissed`,
          {
            application_id: application_id,
            exams: exams[i],
          }
        );
        if (res.status === 200 && this.state.noRoomsApplication) {
          this.setState({
            noRoomsApplication: this.state.noRoomsApplication?.filter(
              (itm) => itm.course_id !== exams[i].course_id
            ),
          });

          this.props.PushPopupAlert({
            title: `Location updated added successfully`,
            theme: PopupAlertTheme.success,
          });
          this.props.LoadCandidateApplications(
            this.props.auth.user.user_id,
            this.setLoading
          );
        }
      } catch (error: any) {
        if (error.response) {
          if (error.response.data) {
            if (error.response.data.errors !== undefined) {
              if (error.response.data.errors.length > 0) {
                if (this.state.noRoomsApplication) {
                  let newArr: sentExamInterface[];
                  if (error.response.data.errors[0].field === undefined) {
                    return this.setState({ redirect: true });
                  } else {
                    newArr = this.state.noRoomsApplication?.filter(
                      (itm) =>
                        itm.course_id ===
                        error.response.data.errors[0].field.course_id
                    );
                    if (newArr === undefined) {
                      return this.setState({ redirect: true });
                    } else {
                      for (let i in newArr) {
                        this.props.removeLocation(newArr[i].course_id);
                      }
                      if (newArr.length === 0) {
                        if (error.response.status === 400) {
                          return this.setState({ redirect: true });
                        }
                      }
                    }
                  }
                } else {
                  if (error.response.status === 400) {
                    this.setState({ redirect: true });
                  }
                }
                this.setState({
                  applicationError: error.response.data.errors[0].message,
                });
                this.props.PushPopupAlert({
                  title: error.response.data.errors[0].message,
                  theme: PopupAlertTheme.warning,
                });
              } else {
                if (error.response.status === 400) {
                  this.setState({ redirect: true });
                }
              }
              this.props.LoadCandidateApplications(
                this.props.auth.user.user_id,
                this.setLoading
              );
            } else {
              if (error.response.status === 400) {
                this.setState({ redirect: true });
              }
              this.setState({
                applicationError: "Something went wrong! Please try again",
              });
              this.props.PushPopupAlert({
                title: "Something went wrong! Please try again",
                theme: PopupAlertTheme.warning,
              });
            }
          }
        }
        console.log("Change error: ", { ...error });
      }
    }
    this.setState({ redirect: true });
    this.props.initiateApplication({
      user_id: this.props.auth.user.user_id,
      selectedPosition: null,
      currentStep: ApplicationStepsTypes.Step1,
      positions: null,
      doTestStatus: {
        status: false,
      },
      selectedLocation: [],
    });
    this.setState({ loading: false });
  };

  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };

  sendApplication = async () => {
    // Validation
    if (
      this.props.auth.isAuthenticated === true &&
      this.props.auth.user.user_id !== ""
    ) {
      if (this.props.application.apply) {
        if (this.props.application.apply.selectedPosition) {
          if (this.props.application.apply.selectedLocation) {
            let exams: sentExamInterface[] = [];
            for (let i in this.props.application.apply.selectedLocation) {
              exams.push({
                course_id:
                  this.props.application.apply.selectedLocation[i].courseId,
                proctoring_type: "center",
                center_id:
                  this.props.application.apply.selectedLocation[i].location
                    .center_id,
                district_id:
                  this.props.application.apply.selectedLocation[i].location
                    .district_id,
              });
            }
            const applicationDetails: SubmitApplicationAPI = {
              user_id: this.props.auth.user.user_id,
              position:
                this.props.application.apply.selectedPosition.position_id,
              application_period: "2021-2022",
              exams: exams,
            };
            this.setState({ loading: true });
            if (this.state.crated_application !== null && exams.length > 0) {
              return this.updateLocation(this.state.crated_application, exams);
            } else {
              try {
                const res = await axios.post<GetApplicationResponse>(
                  `${API.APPLICATION}/application`,
                  applicationDetails
                );
                if (res.status === 200) {
                  if (res.data.noRooms) {
                    if (res.data.noRooms.length > 0) {
                      this.setState({ redirect: false });
                      this.setState({
                        crated_application: res.data.application_id,
                      });
                      this.setState({ noRoomsApplication: res.data.noRooms });
                      for (let i in res.data.noRooms) {
                        this.props.removeLocation(
                          res.data.noRooms[i].course_id
                        );
                      }
                      return this.setState({ loading: false });
                    } else {
                      this.setState({ redirect: true });
                    }
                  } else {
                    this.setState({ redirect: true });
                  }

                  this.props.PushPopupAlert({
                    title: `Application created added successfully`,
                    theme: PopupAlertTheme.success,
                  });
                  this.props.initiateApplication({
                    user_id: this.props.auth.user.user_id,
                    selectedPosition: null,
                    currentStep: ApplicationStepsTypes.Step1,
                    positions: null,
                    doTestStatus: {
                      status: false,
                    },
                    selectedLocation: [],
                  });
                }
                this.props.LoadCandidateApplications(
                  this.props.auth.user.user_id,
                  this.setLoading
                );
              } catch (error: any) {
                console.log("Submit Application err: ", { ...error });
                if (error.response) {
                  if (error.response.data) {
                    if (error.response.data.errors !== undefined) {
                      if (error.response.data.errors.length > 0) {
                        this.setState({
                          applicationError:
                            error.response.data.errors[0].message,
                        });

                        this.props.PushPopupAlert({
                          title: error.response.data.errors[0].message,
                          theme: PopupAlertTheme.warning,
                        });
                        if (error.response.status === 400) {
                          this.setState({ redirect: true });
                          this.props.initiateApplication({
                            user_id: this.props.auth.user.user_id,
                            selectedPosition: null,
                            currentStep: ApplicationStepsTypes.Step1,
                            positions: null,
                            doTestStatus: {
                              status: false,
                            },
                            selectedLocation: [],
                          });
                          this.props.LoadCandidateApplications(
                            this.props.auth.user.user_id,
                            this.setLoading
                          );
                        }
                      }
                    } else {
                      this.props.PushPopupAlert({
                        title: "Something went wrong! Please try again",
                        theme: PopupAlertTheme.danger,
                      });
                      this.setState({
                        applicationError:
                          "Something went wrong! Please try again",
                      });
                    }
                  }
                }
              }
            }
            this.setState({ loading: false });
          } else {
            this.setState({
              applicationError: "Please select location for exam!!!",
            });
          }
        } else {
          this.setState({ applicationError: "Please select position!!!" });
        }
      } else {
        this.setState({
          applicationError: "Please initiate the application again!!!",
        });
      }
    } else {
      this.setState({ applicationError: "You are not authenticated!!!" });
    }
  };

  componentDidMount = () => {
    this.loadMyApplications();
    this.LoadLocations();
  };
  render() {
    if (this.state.redirect === true) {
      return <Redirect to="/applications" />;
    }
    if (this.state.loading === true) {
      return (
        <div className="bg-white rounded px-4 py-4 flex flex-col">
          <span className="mx-4 animate-pulse">Loading, Please wait...</span>
          <Loading />
        </div>
      );
    }

    return (
      <Fragment>
        <div className="bg-white px-2 py-2 rounded shadow">
          <div className="bg-white flex flex-col mb-5 ml-2 mt-2">
            <div className="flex flex-row gap-3">
              <MdLocationOn className="hidden md:block text-5xl text-blue-600" />
              <div className="flex flex-col justify-center">
                <span className="text-xl md:text-xl font-bold text-gray-700">
                  Position: {this.props.currentPosition?.position_title}
                </span>
                <span className="text-gray-500 text-sm italic">
                  Choose exam location for each course
                </span>
                {/* <span className="text-xs text-gray-500">For the corresponding exam course, choose the location where you want to do the exam, you can make changes anytime even after completing the process but before the exam date</span> */}
              </div>
            </div>
          </div>
          {this.props.application.apply &&
            this.props.application.apply.selectedPosition &&
            this.props.application.apply.selectedPosition.courses.map(
              (item, i) => (
                <div
                  onClick={() => this.setState({ selectedCourse: item })}
                  key={i + 1}
                  className={`${
                    this.courseStatus(item) !== undefined
                      ? "bg-blue-100 border border-blue-300"
                      : "bg-gray-200"
                  } rounded px-2 py-2 flex flex-col md:flex-row justify-start md:justify-between items-start md:items-center my-3`}
                >
                  <div className="flex flex-row items-center gap-3 w-full">
                    <div
                      className={`${
                        this.courseStatus(item) !== undefined
                          ? "bg-blue-600"
                          : "bg-gray-600"
                      } h-8 w-8 rounded-full flex justify-center items-center text-white font-bold px-3 py-3`}
                    >
                      {i + 1}
                    </div>
                    <div className="flex flex-col">
                      <span
                        className={`text-sm md:text-lg font-bold ${
                          this.courseStatus(item) !== undefined
                            ? "text-blue-700"
                            : ""
                        }`}
                      >
                        Course: {item.course_code}-{item.course_name}
                      </span>
                      <span className="text-xs text-gray-600">
                        {this.courseStatus(item) !== undefined ? (
                          <span className="font-bold text-gray-700 text-sm flex">
                            <MdLocationOn className="text-lg" /> Province:{" "}
                            {this.courseStatus(item)?.location.province_name},
                            District:{" "}
                            {this.courseStatus(item)?.location.district_name},
                            Center:{" "}
                            {this.courseStatus(item)?.location.center_name}
                          </span>
                        ) : (
                          <span className="italic text-sm text-gray-500">
                            {this.state.noRoomsApplication?.find(
                              (itm) => itm.course_id === item.course_id
                            ) ? (
                              <span className="text-yellow-600 font-bold">
                                No room found in selected center, please choose
                                another center!
                              </span>
                            ) : (
                              <span>No location selected!</span>
                            )}
                          </span>
                        )}
                      </span>
                    </div>
                  </div>
                  <div className="w-full md:w-max">
                    <button
                      onClick={() => this.setState({ centersModal: true })}
                      className={`btn cursor-pointer w-full md:w-max mt-2 md:mt-0 ${
                        this.courseStatus(item) !== undefined
                          ? "bg-white text-blue-600 border border-blue-600"
                          : "btn-blue"
                      }`}
                    >
                      {this.courseStatus(item) !== undefined
                        ? "Change"
                        : "Choose"}
                    </button>
                  </div>
                </div>
              )
            )}
          {this.state.applicationError !== null &&
            this.state.applicationError !== "" && (
              <Alert
                title="Error"
                description={this.state.applicationError}
                theme={AlertInterface.danger}
              />
            )}
          <div className="flex flex-row justify-between items-center mx-2 my-2">
            <div className="flex flex-row items-center gap-2">
              {/* <button className="btn bg-white text-gray-600 border border-gray-300">Cancel</button> */}
              <button
                onClick={() =>
                  this.props.setApplicationStep(ApplicationStepsTypes.Step1)
                }
                className="btn bg-white text-gray-600 border border-gray-300"
              >
                Back
              </button>
            </div>
            {this.props.application.apply &&
              this.props.application.apply.selectedPosition &&
              this.locationStatus(
                this.props.application.apply.selectedPosition.courses
              ) === true && (
                <div
                  onClick={this.sendApplication}
                  className="btn bg-green-500 hover:bg-green-600 text-white cursor-pointer"
                >
                  Finish
                </div>
              )}
          </div>
        </div>
        {this.state.centersModal === true &&
          this.state.selectedCourse !== null && (
            <Modal
              title={
                <div className="text-gray-800 font-bold text-2xl">
                  Choose exam center location
                </div>
              }
              backDrop={true}
              theme={Themes.default}
              close={() => this.setState({ centersModal: false })}
              backDropClose={false}
              widthSizeClass={ModalSize.maxWidth}
              displayClose={true}
              padding={{ title: true }}
            >
              {this.props.application.apply &&
                this.props.application.apply.selectedPosition && (
                  <ChooseExamCenter
                    positionCourses={this.state.selectedCourse}
                    addLocation={this.props.addLocation}
                    closeModal={this.closeModal}
                  />
                )}
            </Modal>
          )}
      </Fragment>
    );
  }
}

const mapStateToProps = ({
  auth,
  application,
  system,
  periods,
  candidateApplications,
}: StoreState): {
  auth: Auth;
  application: applicationStoreInterface;
  system: System;
  periods: applicationPeriodsStoreInterface;
  candidateApplications: CandidateApplicationsStoreInterface;
} => {
  return {
    auth: auth,
    application: application,
    system: system,
    periods: periods,
    candidateApplications,
  };
};

const ExamCenter = connect(mapStateToProps, {
  initiateApplication,
  selectPosition,
  createApplication,
  addLocation,
  removeLocation,
  PushPopupAlert,
  LoadCandidateApplications,
  AddUserApplication,
})(_ExamCenter);

export default ExamCenter;
