import { Component } from "react";
import { Fragment } from "react";
import { IoIosBriefcase } from "react-icons/io";
import * as Interfaces from "../../../interfaces/index";
import { PositionAPI } from "../../../interfaces/index";
import searchData from "../../../utils/search";
import {
  applicationPeriodsStoreInterface,
  initialApplicationInterface,
  System,
} from "../../../actions";
import LoadingV2, { LoadingType } from "../../../components/Loading/LoadingV2";
import { connect } from "react-redux";
import {
  Auth,
  loadAllPositions,
  setSelectedPosition,
  positionsStoreInterface,
  loadEducationCategories,
  loadSchoolLevels,
  setSelectedEducationCategory,
  setSelectedSchoolLevel,
  PushPopupAlert,
  FC_GetProfileDetails,
  loadApplicationPeriods,
  initiateApplication,
} from "../../../actions";
import { StoreState } from "../../../reducers";
import { schoolLevelsStoreInterface } from "../../../actions/school_levels";
import { Link } from "react-router-dom";
import NoResult, {
  NoResultThemes,
} from "../../../components/NoResult/NoResult";
import { PopupAlertData } from "../../../components/Alert/Popup";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import Loading from "../../../components/Loading/Loading";
import { RiSearchLine } from "react-icons/ri";
import { ApplicationStepsTypes } from "../ApplicationSteps/ApplicationSteps";
import { AiOutlineReload } from "react-icons/ai";

interface ApplicationPositionsProps {
  auth: Auth;
  currentPosition: Interfaces.PositionAPI | null;
  setCurrentPosition: (position: Interfaces.PositionAPI) => void;
  positions: positionsStoreInterface;
  loadAllPositions: (callback: (loading: boolean) => void) => void;
  setSelectedPosition: (position: PositionAPI) => void;
  schoolLevels: schoolLevelsStoreInterface;
  loadEducationCategories: (callback: (loading: boolean) => void) => void;
  loadSchoolLevels: (callback: (loading: boolean) => void) => void;
  setSelectedEducationCategory: (
    education_category: Interfaces.education_category
  ) => void;
  setSelectedSchoolLevel: (education_category: Interfaces.school_level) => void;
  PushPopupAlert: (alert: PopupAlertData) => void;
  FC_GetProfileDetails: Function;
  periods: applicationPeriodsStoreInterface;
  loadApplicationPeriods: (callback: (loading: boolean) => void) => void;
  initiateApplication: (initial: initialApplicationInterface) => void;
}

interface ApplicationPositionsState {
  schoolLevelsFilter: boolean;
  searchLevel: searchItem | null;
  filteredPositions: Interfaces.PositionAPI[] | null;
  searchPosition: string;
  showFilter: boolean;
  loading: boolean;
}

export interface searchItem {
  educationCategory: Interfaces.education_category | null;
  schoolLevel: Interfaces.school_level | null;
}

class _ApplicationPositions extends Component<
  ApplicationPositionsProps,
  ApplicationPositionsState
> {
  constructor(props: ApplicationPositionsProps) {
    super(props);

    this.state = {
      schoolLevelsFilter: false,
      searchLevel: null,
      filteredPositions: null,
      searchPosition: "",
      showFilter: true,
      loading: false,
    };
  }
  setLoadingState = (state: boolean) => {
    this.setState({ loading: state });
  };

  setSchoolLevelFilterFn = (status: boolean) => {
    // this.setState({ schoolLevelsFilter: status });
    this.setState({ showFilter: status });
  };
  setSearchLevel = (
    educationCategory: Interfaces.education_category | null,
    schoolLevel: Interfaces.school_level | null
  ) => {
    this.setState({
      searchLevel: {
        educationCategory: educationCategory,
        schoolLevel: schoolLevel,
      },
    });
    if (educationCategory) {
      this.props.setSelectedEducationCategory(educationCategory);
    }
    if (schoolLevel) {
      this.props.setSelectedSchoolLevel(schoolLevel);
      this.setState({ showFilter: false });
    }
    this.filterPositions(this.props.positions.positions_list, {
      educationCategory: educationCategory,
      schoolLevel: schoolLevel,
    });
  };

  filterPositions = (
    myDATA: PositionAPI[] | null,
    searchItem: searchItem | null
  ) => {
    let result: PositionAPI[] | null = myDATA;
    if (myDATA !== null) {
      if (searchItem !== null) {
        if (searchItem.educationCategory !== null) {
          result = myDATA.filter(
            (itm) =>
              itm.education_institute_id.education_institute_id ===
              searchItem.educationCategory?.education_institute_id
          );
        }
        if (searchItem.schoolLevel !== null) {
          let selected = myDATA.filter(
            (itm) =>
              itm.school_level_id.school_level_id ===
              searchItem.schoolLevel?.school_level_id
          );
          if (this.state.searchLevel !== null) {
            if (
              this.state.searchLevel?.educationCategory !== null &&
              this.state.searchLevel?.educationCategory !== undefined
            ) {
              result = selected.filter(
                (itm) =>
                  itm.education_institute_id.education_institute_id ===
                  this.state.searchLevel?.educationCategory
                    ?.education_institute_id
              );
            }
          }
        }
      }
    }
    return result;
  };

  componentDidMount = () => {
    if (
      this.props.periods.selected_period === null ||
      this.props.periods.selected_period.is_active === false
    ) {
      this.props.loadApplicationPeriods(this.setLoadingState);
    }

    if (
      this.props.schoolLevels.education_categories === null ||
      this.props.schoolLevels.education_categories.length === 0
    ) {
      this.props.loadEducationCategories(this.setLoadingState);
    }
    if (
      this.props.schoolLevels.school_levels === null ||
      this.props.schoolLevels.school_levels.length === 0
    ) {
      this.props.loadSchoolLevels(this.setLoadingState);
    }
    if (
      this.props.positions.positions_list === null ||
      this.props.positions.positions_list.length === 0
    ) {
      this.props.loadAllPositions(this.setLoadingState);
    }
    this.setState({
      searchLevel: {
        educationCategory: this.props.schoolLevels.selectedEducationCategory,
        schoolLevel: this.props.schoolLevels.selectedSchoolLevel,
      },
    });
    if (this.props.schoolLevels.selectedEducationCategory !== null) {
      this.setState({ showFilter: false });
    }
    if (this.props.schoolLevels.selectedSchoolLevel !== null) {
      this.setState({ showFilter: false });
    }
    if (
      this.filterPositions(
        this.props.positions.positions_list,
        this.state.searchLevel
      )?.length === 0
    ) {
      this.props.PushPopupAlert({
        title: "No vacancies found!",
        description:
          "No vacancies for the selected school level, please make another filter",
        theme: PopupAlertTheme.danger,
      });
      this.setState({ showFilter: true });
    }
    if (
      this.props.positions.positions_list !== null &&
      this.props.positions.positions_list.length === 0
    ) {
      this.props.PushPopupAlert({
        title: "No vacancies found!",
        description:
          "No vacancies for this time, please wait for next recruitment period",
        theme: PopupAlertTheme.info,
      });
    }
    if (this.props.auth.documents.length === 0) {
      this.setState({
        loading: true,
      });
      this.props.FC_GetProfileDetails(
        this.props.auth.user.user_id,
        (status: boolean, msg: string) => {
          if (status === true) {
            this.setState({
              loading: false,
            });
          } else {
            this.setState({
              loading: false,
            });
          }
        }
      );
    }
  };

  render() {
    // localStorage.removeItem(ActionTypes.ADD_ANSWER);
    // if (this.props.periods.applicationPeriods !== null) {
    //   if (this.props.periods.applicationPeriods.filter(itm => itm.is_active === true).length === 0) {
    //     return <Redirect to="/applications" />;
    //   }
    // }
    return (
      <Fragment>
        <div className="w-full">
          <div className="grid md:grid-cols-12">
            <div
              className={`${
                this.state.schoolLevelsFilter === true
                  ? "col-span-12"
                  : "col-span-12"
              } h-full`}
            >
              <div className="">
                {this.props.positions.positions_list !== null &&
                  this.props.positions.positions_list.length > 0 && (
                    <>
                      <div className="flex flex-col gap-1 bg-white px-3 pt-2 pb-3 rounded shadow">
                        <div className="flex flex-col">
                          {/* <span className="text-lg text-blue-600 font-bold">Filter positions</span> */}
                        </div>
                        <div className="flex flex-row items-end">
                          <div className="flex flex-col md:flex-row items-end gap-3 w-full">
                            {this.props.schoolLevels.education_categories ===
                            null ? (
                              <Loading />
                            ) : (
                              <div className="flex flex-col w-full md:w-1/3">
                                <span className="font-bold text-sm">
                                  Filter by Institute
                                </span>
                                <select
                                  onChange={(e) => {
                                    let selected =
                                      this.props.schoolLevels.education_categories?.find(
                                        (itm) =>
                                          itm.education_institute_id ===
                                          e.target.value
                                      );
                                    this.setSearchLevel(
                                      selected !== undefined ? selected : null,
                                      this.state.searchLevel?.schoolLevel !==
                                        undefined
                                        ? this.state.searchLevel?.schoolLevel
                                        : null
                                    );
                                  }}
                                  className="input-white border border-gray-400"
                                >
                                  <option value="">
                                    Choose Institute name
                                  </option>
                                  <option value="">All </option>
                                  {this.props.schoolLevels.education_categories.map(
                                    (item, i) => (
                                      <option
                                        value={item.education_institute_id}
                                        key={i + 1}
                                      >
                                        {item.education_category_title}
                                      </option>
                                    )
                                  )}
                                </select>
                              </div>
                            )}
                            {this.props.schoolLevels.school_levels === null ? (
                              <Loading />
                            ) : (
                              <div className="flex flex-col w-full md:w-1/3">
                                <span className="font-bold text-sm">
                                  Filter by education level
                                </span>
                                <select
                                  onChange={(e) => {
                                    let selected =
                                      this.props.schoolLevels.school_levels?.find(
                                        (itm) =>
                                          itm.school_level_id === e.target.value
                                      );
                                    this.setSearchLevel(
                                      this.state.searchLevel
                                        ?.educationCategory !== undefined
                                        ? this.state.searchLevel
                                            ?.educationCategory
                                        : null,
                                      selected !== undefined ? selected : null
                                    );
                                  }}
                                  className="input-white border border-gray-400"
                                >
                                  <option value="">
                                    Choose education level
                                  </option>
                                  <option value="">All</option>
                                  {this.props.schoolLevels.school_levels.map(
                                    (item, i) => (
                                      <option
                                        value={item.school_level_id}
                                        key={i + 1}
                                      >
                                        {item.school_level_name}
                                      </option>
                                    )
                                  )}
                                </select>
                              </div>
                            )}
                            {this.props.schoolLevels.education_categories ===
                              null ||
                            this.props.schoolLevels.school_levels === null ? (
                              <Loading />
                            ) : (
                              <input
                                onChange={(e) =>
                                  this.setState({
                                    searchPosition: e.target.value,
                                  })
                                }
                                value={this.state.searchPosition}
                                type="text"
                                placeholder="Search a position name"
                                className="input-white border border-gray-400 h-10 md:w-1/3"
                              />
                            )}
                          </div>
                        </div>
                        {this.state.searchLevel !== null &&
                          (this.state.searchLevel.educationCategory ||
                            this.state.searchLevel.schoolLevel) && (
                            <div className="flex flex-row gap-3 mt-2 mx-1 rounded font-bold">
                              <RiSearchLine className="text-xl -mr-2" />
                              <span className="text-gray-600 font text-sm italic">
                                Results for:{" "}
                              </span>
                              <div className="flex flex-row justify-start items-center">
                                <span className="text-sm md:text-sm my-0 font-bold text-blue-600 underline">
                                  {this.state.searchLevel !== null &&
                                    this.state.searchLevel.educationCategory
                                      ?.education_category_title}
                                  {this.state.searchLevel?.schoolLevel !==
                                    null &&
                                    this.state.searchLevel?.schoolLevel !==
                                      undefined &&
                                    " - " +
                                      this.state.searchLevel.schoolLevel
                                        ?.school_level_name}
                                </span>
                              </div>
                            </div>
                          )}
                      </div>
                    </>
                  )}
                <div className="mt-2">
                  <div className="flex flex-col gap-2">
                    {this.filterPositions(
                      this.props.positions.positions_list,
                      this.state.searchLevel
                    ) === null || this.state.loading === true ? (
                      <LoadingV2 type={LoadingType.dark} />
                    ) : this.filterPositions(
                        this.props.positions.positions_list,
                        this.state.searchLevel
                      )!.length === 0 ? (
                      this.props.positions.positions_list !== null &&
                      this.props.positions.positions_list.length === 0 ? (
                        <div className="bg-yellow-50 text-yellow-700 rounded px-3 py-4 font-bold">
                          Please, There are no vacancies available this time,
                          wait for publication!
                        </div>
                      ) : (
                        <NoResult
                          theme={NoResultThemes.Dark}
                          value="No vacancies found!"
                          component={
                            <div className="flex flex-col items-center justify-center gap-3">
                              <Link
                                to="/applications"
                                className="btn bg-gray-50 text-gray-700 text-sm  hover:bg-blue-100 hover:border-blue-700 hover:text-blue-900 animate__animated animate__zoomIn"
                              >
                                View my applications
                              </Link>
                            </div>
                          }
                        />
                      )
                    ) : (
                      searchData(
                        this.filterPositions(
                          this.props.positions.positions_list,
                          this.state.searchLevel
                        )!,
                        this.state.searchPosition
                      ).map((item, key) => (
                        <div
                          key={key + 1}
                          onClick={() => {
                            this.props.initiateApplication({
                              user_id: this.props.auth.user.user_id
                                ? this.props.auth.user.user_id
                                : "waiting",
                              selectedPosition: null,
                              currentStep: ApplicationStepsTypes.Step1,
                              positions: null,
                              doTestStatus: {
                                status: false,
                              },
                              selectedLocation: [],
                            });
                            this.props.setCurrentPosition(item);
                          }}
                          title="Click to apply for this position"
                          className="bg-white cursor-pointer rounded-md  border-2 border-gray-200 hover:border-blue-500 hover:bg-white animate__animated animate__fadeIn animate__fast"
                        >
                          <div className="grid lg:grid-cols-12 group px-3 py-2 rounded-md">
                            <div className="col-span-12 lg:col-span-10">
                              <div className="flex flex-row items-center gap-3">
                                <div>
                                  <IoIosBriefcase className="text-4xl md:text-5xl text-blue-600" />
                                </div>
                                <div className="flex flex-col">
                                  <span className="text-xs lg:text-sm font-bold text-gray-700">
                                    {item.position_title}
                                  </span>
                                  <span className="text-xs text-gray-500 italic">
                                    School level:{" "}
                                    <span
                                      className="text-gray-800 font-light"
                                      style={{ fontStyle: "normal" }}
                                    >
                                      {item.school_level_id.school_level_name}
                                    </span>
                                  </span>
                                  <span className="text-xs font-bold text-blue-600 bg-blue-100 w-max px-3 py-1 rounded-full">
                                    {
                                      item.education_institute_id
                                        .education_category_title
                                    }
                                  </span>
                                </div>
                              </div>
                            </div>
                            <div className="col-span-12 lg:col-span-2 hidden lg:block">
                              <div className="h-full flex items-center justify-end">
                                <div className="btn bg-blue-50 text-blue-600 group-hover:text-white group-hover:bg-blue-600 float-right">
                                  Apply
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                      ))
                    )}
                    <div
                      title="Click here to check out new changes"
                      className="bg-blue-100 text-blue-800 border-2 border-blue-600 hover:bg-white hover:text-blue-600 px-4 py-4 rounded-lg cursor-pointer mb-10 text-center font-bold text-2xl flex flex-row items-center justify-center gap-3"
                      onClick={() =>
                        this.props.loadAllPositions(this.setLoadingState)
                      }
                    >
                      <div className="px-1 py-1">
                        <AiOutlineReload className="text-4xl" />
                      </div>
                      <span>Reload for new updates</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Fragment>
    );
  }
}

const mapStateToProps = ({
  auth,
  positions,
  schoolLevels,
  system,
  periods,
}: StoreState): {
  auth: Auth;
  positions: positionsStoreInterface;
  schoolLevels: schoolLevelsStoreInterface;
  system: System;
  periods: applicationPeriodsStoreInterface;
} => {
  return {
    auth: auth,
    positions: positions,
    schoolLevels: schoolLevels,
    system: system,
    periods: periods,
  };
};

const ApplicationPositions = connect(mapStateToProps, {
  loadAllPositions,
  setSelectedPosition,
  loadEducationCategories,
  loadSchoolLevels,
  setSelectedEducationCategory,
  setSelectedSchoolLevel,
  PushPopupAlert,
  FC_GetProfileDetails,
  loadApplicationPeriods,
  initiateApplication,
})(_ApplicationPositions);

export default ApplicationPositions;
