import { ActionTypes } from "../actions";
import { WaitingListActionType } from "../actions/waitingList";
import { WaitingListInterface } from "../interfaces/waitingList";

export const waitingListReducer = (
  state: WaitingListInterface[] | null = null,
  action: WaitingListActionType
) => {
  // let index;
  switch (action.type) {
    case ActionTypes.GET_WAITING_LIST:
      return action.payload;
    default:
      return state;
  }
};
