export const InitializeExamDetails = (
  periodId: string,
  courseId: string,
  district_id: string,
  center_id: string,
  room_id: string,
  position_id: string,
  callback: (loading: boolean) => void
) => {
  return async (dispatch: Dispatch) => {
    callback(true);
    try {
      const res = await axios.get<InitialExamDetails>(
        `${API.QBank}/exam/details/${periodId}/${courseId}`
      );
      dispatch<setInitialExamDetailsActions>({
        type: ActionTypes.SET_INITIAL_EXAM_DETAILS,
        payload: {
          res: res.data,
          position_id: position_id,
          period_id: periodId,
          room_id: room_id,
          center_id: center_id,
          district_id: district_id,
        },
      });
      dispatch<AddExamRules>({
        type: ActionTypes.ADD_EXAM_RULES,
        payload: res.data.rules as ExamRules,
      });
    } catch (error: any) {
      console.log("Err: ", { ...error });
    }
    callback(false);
  };
};

export const RequestUserExamDetails = (
  userExamDetails: userExamDetails,
  callback: (loading: boolean) => void,
  actionStatus: (msg: string, status: boolean) => void,
  BackToExamination: (exam: ExamResultsDetails) => void
) => {
  return async (dispatch: Dispatch) => {
    callback(true);
    try {
      const res = await axios.post<any>(
        `${API.DO_EXAM}/userquestions`,
        userExamDetails
      );
      console.log("Load prev exam: ", res.data);
      if (res.status !== 200) {
        let FirstTry: userExamDetailsResponse = res.data;
        dispatch<setExamDetailsActions>({
          type: ActionTypes.SET_USER_EXAM_DETAILS,
          payload: FirstTry,
        });
        actionStatus("Exam loaded successfully!", true);
      } else {
        BackToExamination(res.data);
        actionStatus("Backing to exam...!", true);
      }
    } catch (error: any) {
      if (error.response) {
        if (error.response.data) {
          if (error.response.data.errors !== undefined) {
            if (error.response.data.errors.length > 0) {
              actionStatus(error.response.data.errors[0].message, false);
            } else {
              actionStatus("Failed to load Question!", false);
            }
          } else {
            actionStatus("Failed to load Question!", false);
          }
        } else {
          actionStatus("Failed to load Question!", false);
        }
      } else {
        actionStatus("Unknown error!", false);
      }
      console.log("Err load: ", { ...error });
      console.log("Err load: ", error);
    }
    callback(false);
  };
};

