export const SubmitExamQuestionAnswer = (
  answer: CandidateExamQuestionAnswer,
  callback: (loading: boolean) => void,
  actionStatus: (msg: string, status: boolean) => void
) => {
  return async (dispatch: Dispatch) => {
    callback(true);
    try {
      const res = await axios.post<any>(
        `${API.DO_EXAM}/questions/answer`,
        answer
      );
      dispatch<submitQuestionAnswerActions>({
        type: ActionTypes.SUBMIT_EXAM_QUESTION_ANSWER,
        payload: answer,
      });
      console.log("Submit answer Response: ", res.data);
      actionStatus("Exam question answer saved successfully!", true);
    } catch (error: any) {
      if (error.response) {
        if (error.response.data) {
          if (error.response.data.errors !== undefined) {
            if (error.response.data.errors.length > 0) {
              actionStatus(error.response.data.errors[0].message, false);
            } else {
              actionStatus("Failed to add Question answer!", false);
            }
          } else {
            actionStatus("Failed to add Question answer!", false);
          }
        } else {
          actionStatus("Failed to add Question answer!", false);
        }
      }
      console.log("Err: ", { ...error });
    }
    callback(false);
  };
};

