export const setExamStatus = (
  exam_id: string,
  action: UserExamActions,
  reason: string,
  callback: (loading: boolean) => void
) => {
  return async (dispatch: Dispatch) => {
    callback(true);
    try {
      await axios.post(`${API.DO_EXAM}/user/update`, {
        exam_id: exam_id,
        action: action,
        reason: reason,
      });
      dispatch<setExamStatusActions>({
        type: ActionTypes.SET_EXAM_STATUS,
        payload: {
          exam_id: exam_id,
          action: action,
          reason: reason,
        },
      });
    } catch (error: any) {
      console.log("Err: invig", { ...error });
    }
    callback(false);
  };
};

