import {
  Action,
  ActionTypes,
  candidateExamStoreInterface,
  QuestionAnswersStore,
} from "../actions";
import { userExamDetailsResponse } from "../interfaces/exam";
import { QuestionInterface, QuestionLanguage } from "../interfaces/question";

// default state
const defaultState: candidateExamStoreInterface = {
  candidate_exam: null,
  selected_exam_id: null,
  selected_question: null,
  selected_language: QuestionLanguage.Eng,
  question_answers: [],
  selected_exam_details: null,
  user_exam_details: null,
  user_exam_results: null,
  user_exams_temp: [],
  user_exams_done: [],
  exam_room_details: null,
};

/**
 * this is the comment that im using from my brand new keyboard and i like i very mutch hahah this is awesome and coool
 * @param state
 * @param action
 * @returns
 */
export const candidateExamReducer = (
  state: candidateExamStoreInterface = defaultState,
  action: Action
) => {
  switch (action.type) {
    case ActionTypes.LOAD_CANDIDATE_EXAM:
      return {
        ...state,
        candidate_exam: action.payload,
        selected_question: action.payload.questions[0].id,
      };
    case ActionTypes.SELECTED_EXAM_ID:
      let my_temp = state.user_exams_temp.find(
        (itm) => itm.id === action.payload
      );
      return {
        ...state,
        selected_exam_id: action.payload,
        selected_exam_details:
          state.user_exams_temp.length === 0
            ? null
            : my_temp !== undefined
            ? my_temp
            : null,
      };
    case ActionTypes.SELECTED_EXAM_QUESTION:
      return {
        ...state,
        selected_question:
          action.payload.exam_id === state.selected_exam_id
            ? action.payload.question_id
            : null,
      };
    case ActionTypes.SET_QUESTION_LANGUAGE:
      return { ...state, selected_language: action.payload };
    case ActionTypes.SET_INITIAL_EXAM_DETAILS:
      let exams_temp_test = state.user_exams_temp.find(
        (itm) => itm.id === action.payload.res.id
      );
      return {
        ...state,
        user_exams_temp:
          exams_temp_test === undefined
            ? [...state.user_exams_temp, action.payload.res]
            : state.user_exams_temp,
        exam_room_details: {
          district_id: action.payload.district_id,
          center_id: action.payload.center_id,
          room_id: action.payload.room_id,
          period_id: action.payload.period_id,
          position_id: action.payload.position_id,
        },
      };
    case ActionTypes.SET_USER_EXAM_DETAILS:
      let newQuestions: QuestionInterface[] = [];
      if (state.candidate_exam !== null) {
        for (let item of action.payload.QuestionsId) {
          let temp = state.candidate_exam.questions.find(
            (itm) => itm.id === item.question_old_id
          );
          if (temp !== undefined) {
            newQuestions = [
              ...newQuestions,
              {
                id: item.question_id,
                course: temp.course,
                question: temp.question,
                type: temp.type,
                chapters: temp.chapters,
                customNumber: temp.customNumber,
                marks: temp.marks,
                answer: temp.answer,
              },
            ];
          }
        }
      }
      return {
        ...state,
        user_exam_details: action.payload,
        candidate_exam:
          newQuestions.length === 0 || state.candidate_exam === null
            ? state.candidate_exam
            : {
                questions: newQuestions,
                status: state.candidate_exam.status,
                period: state.candidate_exam.period,
                course: state.candidate_exam.course,
                exam_status: state.candidate_exam.exam_status,
                termes: state.candidate_exam.termes,
                questions_per_page: state.candidate_exam.questions_per_page,
                exam_date: state.candidate_exam.exam_date,
                start_time: state.candidate_exam.start_time,
                end_time: state.candidate_exam.end_time,
                duration: state.candidate_exam.duration,
                number_format: state.candidate_exam.number_format,
                discription: state.candidate_exam.discription,
                createdAt: state.candidate_exam.createdAt,
                updatedAt: state.candidate_exam.updatedAt,
                id: action.payload.ExamId.exam_id,
                rules: state.candidate_exam.rules,
              },
        selected_exam_id: action.payload.ExamId.exam_id,
        selected_exam_details:
          state.selected_exam_details === null
            ? state.selected_exam_details
            : {
                questions: newQuestions,
                status: state.selected_exam_details.status,
                period: state.selected_exam_details.period,
                course: state.selected_exam_details.course,
                exam_status: state.selected_exam_details.exam_status,
                termes: state.selected_exam_details.termes,
                questions_per_page:
                  state.selected_exam_details.questions_per_page,
                exam_date: state.selected_exam_details.exam_date,
                start_time: state.selected_exam_details.start_time,
                end_time: state.selected_exam_details.end_time,
                duration: state.selected_exam_details.duration,
                number_format: state.selected_exam_details.number_format,
                discription: state.selected_exam_details.discription,
                createdAt: state.selected_exam_details.createdAt,
                updatedAt: state.selected_exam_details.updatedAt,
                id: action.payload.ExamId.exam_id,
                rules: state.selected_exam_details.rules,
              },
        selected_question: action.payload.QuestionsId[0].question_id,
      };
    case ActionTypes.SET_EXAM_QUESTION_ANSWER:
      return {
        ...state,
        question_answers:
          state.question_answers.filter(
            (itm) => itm.question_id === action.payload.question_id
          ).length === 0
            ? [...state.question_answers, action.payload]
            : [
                ...state.question_answers.filter(
                  (itm1) => itm1.question_id !== action.payload.question_id
                ),
                action.payload,
              ],
      };
    case ActionTypes.CLEAN_CANDIDATE_EXAM:
      return {
        ...state,
        selected_exam_details: null,
        selected_exam_id:
          state.selected_exam_id === action.payload
            ? null
            : state.selected_exam_id,
        user_exam_details: null,
        candidate_exam: null,
        selected_question: null,
      };
    case ActionTypes.CLEAN_CANDIDATE_EXAM_ANSWERS:
      return {
        ...state,
        question_answers: [],
      };
    case ActionTypes.SUBMIT_EXAM_QUESTION_ANSWER:
      let newQuestionsAnswers: QuestionAnswersStore[] = [];
      for (let currentAnswer of state.question_answers) {
        if (currentAnswer.question_id === action.payload.question) {
          newQuestionsAnswers = [
            ...newQuestionsAnswers,
            {
              question_id: currentAnswer.question_id,
              status: true,
              answer: currentAnswer.answer,
            },
          ];
        } else {
          newQuestionsAnswers = [...newQuestionsAnswers, currentAnswer];
        }
      }
      return {
        ...state,
        question_answers:
          newQuestionsAnswers.length > 0
            ? newQuestionsAnswers
            : state.question_answers,
      };
    case ActionTypes.GET_USER_EXAM_DETAILS:
      return { ...state, user_exam_results: action.payload };
    case ActionTypes.LOAD_EXAMS_DONE:
      return { ...state, user_exams_done: action.payload };
    case ActionTypes.BACK_TO_EXAMINATION:
      let addedQuestions = action.payload.questions;
      let userExamDet: userExamDetailsResponse | null = null;
      let userExamDetQuestionIds: {
        question_id: string;
        question_old_id: string;
      }[] = [];
      for (let e of addedQuestions) {
        userExamDetQuestionIds = [
          ...userExamDetQuestionIds,
          {
            question_id: e.question_id,
            question_old_id: e.id,
          },
        ];
      }
      userExamDet = {
        ExamId: {
          exam_id: action.payload.exam_id,
          exam_old_id: action.payload.id,
        },
        QuestionsId: userExamDetQuestionIds,
        status: action.payload.exam_status,
      };
      let answersAdded: QuestionAnswersStore[] = [];
      for (let it of addedQuestions) {
        if (
          state.question_answers.find(
            (iii) => iii.question_id === it.question_id
          )?.question_id === undefined
        ) {
          let test = it.question.find(
            (x) => x.language === state.selected_language
          );
          if (test !== undefined) {
            answersAdded = [
              ...answersAdded,
              {
                question_id: it.question_id,
                status: true,
                answer: it.question.find(
                  (x) => x.language === state.selected_language
                )!.user_answer, // Check this!!!!
              },
            ];
          }
        }
      }

      let newQuestions1: QuestionInterface[] = [];
      if (state.candidate_exam !== null) {
        for (let item of userExamDet.QuestionsId) {
          let temp = state.candidate_exam.questions.find(
            (itm) => itm.id === item.question_old_id
          );
          if (temp !== undefined) {
            newQuestions1 = [
              ...newQuestions1,
              {
                id: item.question_id,
                course: temp.course,
                question: temp.question,
                type: temp.type,
                chapters: temp.chapters,
                customNumber: temp.customNumber,
                marks: temp.marks,
                answer: temp.answer,
              },
            ];
          }
        }
      }
      return {
        ...state,
        user_exam_details: userExamDet,
        candidate_exam:
          newQuestions1.length === 0 || state.candidate_exam === null
            ? state.candidate_exam
            : {
                questions: newQuestions1,
                status: state.candidate_exam.status,
                period: state.candidate_exam.period,
                course: state.candidate_exam.course,
                exam_status: state.candidate_exam.exam_status,
                termes: state.candidate_exam.termes,
                questions_per_page: state.candidate_exam.questions_per_page,
                exam_date: state.candidate_exam.exam_date,
                start_time: state.candidate_exam.start_time,
                end_time: state.candidate_exam.end_time,
                duration: state.candidate_exam.duration,
                number_format: state.candidate_exam.number_format,
                discription: state.candidate_exam.discription,
                createdAt: state.candidate_exam.createdAt,
                updatedAt: state.candidate_exam.updatedAt,
                id: userExamDet.ExamId.exam_id,
                rules: state.candidate_exam.rules,
              },
        selected_exam_id: userExamDet.ExamId.exam_id,
        selected_exam_details:
          state.selected_exam_details === null
            ? state.selected_exam_details
            : {
                questions: newQuestions1,
                status: state.selected_exam_details.status,
                period: state.selected_exam_details.period,
                course: state.selected_exam_details.course,
                exam_status: state.selected_exam_details.exam_status,
                termes: state.selected_exam_details.termes,
                questions_per_page:
                  state.selected_exam_details.questions_per_page,
                exam_date: state.selected_exam_details.exam_date,
                start_time: state.selected_exam_details.start_time,
                end_time: state.selected_exam_details.end_time,
                duration: state.selected_exam_details.duration,
                number_format: state.selected_exam_details.number_format,
                discription: state.selected_exam_details.discription,
                createdAt: state.selected_exam_details.createdAt,
                updatedAt: state.selected_exam_details.updatedAt,
                id: userExamDet.ExamId.exam_id,
                rules: state.selected_exam_details.rules,
              },
        selected_question: userExamDet.QuestionsId[0].question_id,
        question_answers: answersAdded,
      };
    default:
      return state;
  }
};
