import React, { Component } from "react";
import ApplicationPositions from "./ApplicationPositions/ApplicationPositions";
import { ApplicationStepsTypes } from "./ApplicationSteps/ApplicationSteps";
import PositionRequirement from "./PositionRequirement/PositionRequirement";
import ExamCenter from "./ExamCenter/ExamCenter";
import * as Interfaces from "../../interfaces/index";
import { connect } from "react-redux";
import {
  Auth,
  examDataInterface,
  loadExam,
  addExamAnswer,
  deleteExamAnswers,
  SubmitExamAnswers,
  initiateApplication,
  selectPosition,
  createApplication,
  setApplicationStep,
  positionsStoreInterface,
  FC_Access,
  applicationPeriodsStoreInterface,
  loadApplicationPeriods,
  setSelectedPeriod,
} from "../../actions";
import { StoreState } from "../../reducers";
import {
  applicationStoreInterface,
  initialApplicationInterface,
} from "../../actions/application";
import {
  ApplicationInterface,
  ApplicationPeriodGet,
} from "../../interfaces/application";
import { PositionAPI } from "../../interfaces/index";
import { Redirect } from "react-router";
import { UserAccessList } from "../../utils/auth_access";
import Loading from "../../components/Loading/Loading";
import { DATE } from "../../utils/functions";

interface ApplicationState {
  currentStep: ApplicationStepsTypes;
  currentPosition: Interfaces.PositionAPI | null;
  redirectState: boolean;
  loading: boolean;
}

interface ApplicationProps {
  auth: Auth;
  application: applicationStoreInterface;
  initiateApplication: (initial: initialApplicationInterface) => void;
  createApplication: (
    user_id: string,
    application: ApplicationInterface
  ) => void;
  setApplicationStep: (step: ApplicationStepsTypes) => void;
  selectPosition: (position: PositionAPI | null) => void;
  FC_Access: Function;
  periods: applicationPeriodsStoreInterface;
  loadApplicationPeriods: (callback: (loading: boolean) => void) => void;
  setSelectedPeriod: (period: ApplicationPeriodGet) => void;
}

export class _Application extends Component<
  ApplicationProps,
  ApplicationState
> {
  constructor(props: ApplicationProps) {
    super(props);

    this.state = {
      currentStep: ApplicationStepsTypes.Step1,
      currentPosition: null,
      redirectState: false,
      loading: false,
    };
  }
  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };
  componentDidMount = () => {
    if (
      this.props.periods.selected_period === null ||
      this.props.periods.selected_period.is_active === false
    ) {
      this.props.loadApplicationPeriods(this.setLoading);
    }
    if (this.props.periods.applicationPeriods !== null) {
      let myFind = this.props.periods.applicationPeriods.find(
        (itm) => itm.is_active === true
      );
      if (myFind !== undefined) {
        this.props.setSelectedPeriod(myFind);
      }
    }
    // this.props.initiateApplication()
    if (this.props.application.apply === undefined) {
      this.props.initiateApplication({
        user_id: this.props.auth.user.user_id
          ? this.props.auth.user.user_id
          : "waiting",
        selectedPosition: null,
        currentStep: ApplicationStepsTypes.Step1,
        positions: null,
        doTestStatus: {
          status: false,
        },
        selectedLocation: [],
      });
      if (
        this.props.application.applications.length > 0 &&
        this.props.application.apply === undefined
      ) {
        this.setState({ redirectState: true });
      } else {
        this.setState({ redirectState: false });
      }
    }
    console.log("Application: ", this.props.application);
  };
  render() {
    // if (this.props.application.applications.length > 0 && this.props.application.apply === undefined) {
    //   return <Redirect to="/applications" />
    // }
    if (this.props.auth.user.user_id) {
      if (!FC_Access(this.props.auth, UserAccessList.MY_APPLICATION).create) {
        return <Redirect to="/apps-page" />;
      }
    }
    return (
      <div
        className={`px-2 ${
          this.props.auth.user.user_id
            ? ""
            : "max-w-7xl mx-auto px-2 md:px-12 lg:px-24"
        }`}
      >
        {/* Step 2 */}
        {this.props.application.apply !== undefined &&
        this.props.application.apply.currentStep ===
          ApplicationStepsTypes.Step2 ? (
          // <DoTest
          //   currentPosition={this.props.application.apply.selectedPosition}
          //   setCurrentPosition={this.props.selectPosition}
          //   setApplicationStep={this.props.setApplicationStep}
          // />
          <div>Do psychometric tex</div>
        ) : (
          this.props.application.apply !== undefined && (
            <div className="">
              {this.props.application.apply.selectedPosition !== null &&
              this.props.application.apply.currentStep ===
                ApplicationStepsTypes.Step1 ? (
                ""
              ) : (
                <div className="mt-2 pt-2 pb-1 border-b-2">
                  <div className="grid md:grid-cols-2 gap-2">
                    <div className="col-span-1 flex flex-col">
                      <span className="text-blue-600 text-2xl lg:text-3xl font-bold mb-1">
                        Vacant positions
                      </span>
                      {this.props.application.apply.selectedPosition !==
                      null ? (
                        <div className="flex flex-row items-center gap-3 text-sm text-gray-600">
                          <div className="text-blue-200 font-bold bg-blue-700 rounded px-2 border border-blue-300">
                            {this.props.application.apply.currentStep ===
                            ApplicationStepsTypes.Step1
                              ? "Step 1 of 2:"
                              : "Step 2 of 2"}
                          </div>{" "}
                          Choosing examination center
                        </div>
                      ) : (
                        <span className="text-gray-600 text-xs lg:text-sm">
                          List of positions ready for the application, choose a
                          position
                        </span>
                      )}
                    </div>
                    {this.state.loading === true ? (
                      <div className="bg-white rounded px-3 pt-3">
                        <Loading />
                      </div>
                    ) : (
                      <div className="col-span-1 flex flex-col md:items-end -mb-2 md:-mt-2">
                        <div className="flex flex-row gap-2 md:text-right">
                          <span className="font-extrabold text-blue-600">
                            {
                              this.props.periods.selected_period
                                ?.application_session_name
                            }
                          </span>
                        </div>
                        <div className="flex flex-row gap-2 text-sm text-right">
                          <span className="text-gray-800">Start date:</span>
                          <span className="font-extrabold">
                            {this.props.periods.selected_period &&
                              DATE(
                                this.props.periods.selected_period
                                  .application_start_date,
                                "DD/MM/YYYY"
                              )}
                          </span>
                        </div>
                        <div className="flex flex-row gap-2 text-sm text-right items-center">
                          <span className="text-gray-800">Ending date:</span>
                          <span className="font-extrabold text-yellow-900 bg-yellow-100 border-2 border-white rounded px-2 py-1">
                            {this.props.periods.selected_period &&
                              DATE(
                                this.props.periods.selected_period
                                  .application_end_date,
                                "DD/MM/YYYY"
                              )}
                          </span>
                        </div>
                      </div>
                    )}
                  </div>
                </div>
              )}

              <div className="my-2">
                <div className="w-full">
                  {/* Step one */}
                  {this.props.application.apply.currentStep ===
                    ApplicationStepsTypes.Step1 &&
                    (this.props.application.apply.selectedPosition === null ? (
                      <ApplicationPositions
                        currentPosition={
                          this.props.application.apply.selectedPosition
                        }
                        setCurrentPosition={this.props.selectPosition}
                      />
                    ) : (
                      <PositionRequirement
                        currentPosition={
                          this.props.application.apply.selectedPosition
                        }
                        setCurrentPosition={this.props.selectPosition}
                        setApplicationStep={this.props.setApplicationStep}
                      />
                    ))}
                  {/* Step 3 */}
                  {this.props.application.apply.currentStep ===
                    ApplicationStepsTypes.Step3 && (
                    <ExamCenter
                      currentPosition={
                        this.props.application.apply.selectedPosition
                      }
                      setCurrentPosition={this.props.selectPosition}
                      setApplicationStep={this.props.setApplicationStep}
                    />
                  )}
                </div>
              </div>
            </div>
          )
        )}
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  doExam,
  application,
  positions,
  periods,
}: StoreState): {
  auth: Auth;
  doExam: examDataInterface;
  application: applicationStoreInterface;
  positions: positionsStoreInterface;
  periods: applicationPeriodsStoreInterface;
} => {
  return {
    auth: auth,
    doExam: doExam,
    application: application,
    positions,
    periods: periods,
  };
};

export const Application = connect(mapStateToProps, {
  loadExam,
  addExamAnswer,
  deleteExamAnswers,
  SubmitExamAnswers,
  initiateApplication,
  selectPosition,
  createApplication,
  setApplicationStep,
  FC_Access,
  loadApplicationPeriods,
  setSelectedPeriod,
})(_Application);
